import AddOperationCommand from "../command/add_operation_command";
import CalcCommandReceiver from "../command/calc_command_receiver";
import DivideOperationCommand from "../command/divide_operation_command";
import EqualOperationCommand from "../command/equal_operation_command";
import MultiplyOperationCommand from "../command/multiply_operation_command";
import NumberCommand from "../command/number_command";
import SubstractOperationCommand from "../command/substract_operation_command";
import CalcModel from "../model/calc_model";
import CalcEquationView from "./calc_equation_view";
import CalcDisplayView from "./clas_display_view";
import InputPanel from "./input_panel";

export default class CalcMainFrame {
  constructor(calculatorDivId) {
    let calculator = document.getElementById(calculatorDivId);

    let equationCanvas = document.createElement("canvas");
    let displayCanvas = document.createElement("canvas");

    let div1 = document.createElement("div");
    let div2 = document.createElement("div");
    let panel1 = document.createElement("div");
    let panel2 = document.createElement("div");
    let panel3 = document.createElement("div");
    let panel4 = document.createElement("div");
    let panel5 = document.createElement("div");
    let panel6 = document.createElement("div");

    div1.appendChild(equationCanvas);
    div2.appendChild(displayCanvas);
    calculator.appendChild(div1);
    calculator.appendChild(div2);
    calculator.appendChild(panel1);
    calculator.appendChild(panel2);
    calculator.appendChild(panel3);
    calculator.appendChild(panel4);
    calculator.appendChild(panel5);
    calculator.appendChild(panel6);

    let displayView = new CalcDisplayView(displayCanvas);
    let equationView = new CalcEquationView(equationCanvas);

    let calcCommandReceiver = new CalcCommandReceiver();
    calcCommandReceiver.setDisplayView(displayView);
    calcCommandReceiver.setEquationView(equationView);

    let fn = function () {
      let element = this;
      let actionCommand = element.textContent || element.innerText;

      let calcCommand = null;

      if (actionCommand === "+") {
        calcCommand = new AddOperationCommand(calcCommandReceiver);
      } else if (actionCommand === "-") {
        calcCommand = new SubstractOperationCommand(calcCommandReceiver);
      } else if (actionCommand === "*") {
        calcCommand = new MultiplyOperationCommand(calcCommandReceiver);
      } else if (actionCommand === "/") {
        calcCommand = new DivideOperationCommand(calcCommandReceiver);
      } else if (actionCommand === "=") {
        calcCommand = new EqualOperationCommand(calcCommandReceiver);
      } else {
        calcCommand = new NumberCommand(calcCommandReceiver, actionCommand);
      }

      if (calcCommand !== null) {
        calcCommand.execute();
      }

      calcCommandReceiver.updateView();
    };
    let inputPanel1 = new InputPanel();
    inputPanel1.initLayout(["%", "SQRT", "POW", "FRAC"], panel1, fn);

    let inputPanel2 = new InputPanel();
    inputPanel2.initLayout(["CE", "C", "BACK", "/"], panel2, fn);

    let inputPanel3 = new InputPanel();
    inputPanel3.initLayout(["7", "8", "9", "*"], panel3, fn);

    let inputPanel4 = new InputPanel();
    inputPanel4.initLayout(["4", "5", "6", "-"], panel4, fn);

    let inputPanel5 = new InputPanel();
    inputPanel5.initLayout(["1", "2", "3", "+"], panel5, fn);

    let inputPanel6 = new InputPanel();
    inputPanel6.initLayout(["+-", "0", ".", "="], panel6, fn);

    let calcModel = new CalcModel();

    displayView.setModel(calcModel);
    equationView.setModel(calcModel);

    displayView.redraw();
    equationView.redraw();

    calcCommandReceiver.setModel(calcModel);
  }
}
