import CalcConstants from "./calc_constants";

export default class CalcEquationView {
  constructor(equationCanvas) {
    this.canvas = equationCanvas;

    this.canvas.width = CalcConstants.VIEW_WIDTH;
    this.canvas.height = CalcConstants.DEFAULT_VIEW_HEIGHT;

    this.canvas.style.border = "1px solid gray";
    this.canvas.style.curosr = "pointer";

    this.ctx = this.canvas.getContext("2d");

    this.model = null;
  }

  redraw() {
    this.ctx.fillStyle = "white";
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

    let euqationText = null;
    if (this.model !== null) {
      euqationText = this.model.getDisplayEquationText();
    }

    if (euqationText === null || euqationText.trim().length <= 0) {
      this.ctx.fillStyle = "black";

      this.ctx.font = CalcConstants.DEFAULT_FONT;

      let textWidth = this.ctx.measureText(euqationText).width;
      this.ctx.fillText(euqationText, this.canvas.width - textWidth, this.canvas.height - CalcConstants.TEXT_BOTTOM_SPACING);
    }
  }

  setModel(model) {
    this.model = model;
  }
}
